const salary = [
	[],
	[]
]

function setYearSalary() {
	for (let i = 1; i <= 20; i++) {
		salary[0].push(i)
	}
	for (let i = 21; i <= 100; i++) {
		if (i % 5 === 0) {
			salary[0].push(i)
		}
	}
	for (let i = 101; i <= 250; i++) {
		if (i % 10 === 0) {
			salary[0].push(i)
		}
	}
	for (let i = 0; i < salary[0].length; i++) {
		console.log(salary[0].length)
		let front = salary[0][i]
		let afterArr = []
		for (let k = 1; k <= 5; k++) {
			if (front <= 20) {
				afterArr.push((front + k * 1) + 'k')
			} else if (front <= 100) {
				afterArr.push((front + k * 5) + 'k')
			} else {
				afterArr.push((front + k * 10) + 'k')
			}
		}
		salary[1][i] = afterArr
		salary[0][i] = front + 'k'
	}
	salary[0].unshift('')
	salary[1][0].unshift([''])
}
setYearSalary()
export const YearSalary = salary

